:root {
  --bg: #f7f7f8;
  --surface: #fff;
  --text: #1f2937;
  --muted-text: #6b7280;
  --border: #e5e7eb;
  --highlight: #fff8e1;
  --accent: #2563eb;
  --compare-widget-bg: #b5c5fa;
  --card-surface: #fff;
  --compare-bg: #f0f0f0;
  --label-bg: #e5e7eb;
  --valueCell-bg: #f9fafb;
}

[data-theme="dark"] {
  --bg: #0f172a;
  --surface: #111827;
  --text: #e5e7eb;
  --muted-text: #9ca3af;
  --border: #1f2937;
  --highlight: #1e293b;
  --accent: #60a5fa;
  --compare-widget-bg: #2563eb;
  --card-surface: #051f74;
  --compare-bg: var(--muted-text);
  --label-bg: #1f2937;
  --valueCell-bg: #111827;
}

html, body {
  background: var(--bg);
  height: 100%;
  color: var(--text);
  margin: 0;
  font-family: system-ui, sans-serif;
  overflow: hidden;
}

.app-container {
  height: 100vh;
  overflow: auto;
}

.header-container {
  color: #fff;
  text-align: center;
  background-color: #2563eb;
  width: 100%;
  padding: 10px;
  font-size: 12px;
  font-weight: bold;
  position: fixed;
  top: 0;
}

.container {
  max-width: 1200px;
  margin: 120px auto auto;
  padding: 20px;
}

.footer-container {
  color: #fff;
  background-color: #1e40af;
  width: 100%;
  padding: 15px;
  bottom: 0;
}

.footer-container div {
  justify-content: center;
  line-height: 20px;
  display: flex;
}

.footer-container p {
  margin: 0 10px;
}

h1, h2 {
  text-align: center;
}

.product-grid {
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 20px;
  display: grid;
}

.compare-widget {
  background: var(--compare-widget-bg);
  cursor: pointer;
  border: 2px solid #000;
  border-radius: 50px;
  width: 20px;
  margin: 10px;
  padding: 15px;
  position: absolute;
  bottom: 50px;
  right: 20px;
}

.card {
  background: var(--card-surface);
  border-radius: 10px;
  flex-direction: column;
  gap: 10px;
  padding: 15px;
  display: flex;
  box-shadow: 0 6px 18px #00000014;
}

.card {
  & img {
    border-radius: 8px;
    width: 40%;
  }

  & h3 {
    margin: 5px 0;
  }

  & p {
    margin: 0;
  }

  & ul {
    list-style-type: none;

    & li {
      margin: 4px 0;
    }

    & span {
      padding-inline-start: 5px;
      font-weight: 200;
    }
  }
}

.remove {
  color: #fff;
  background: #ef4444;
}

.add {
  color: #fff;
  background: #2563eb;
}

.price {
  font-weight: bold;
}

.card button {
  color: #fff;
  cursor: pointer;
  border: 2px solid #e5e7eb;
  border-radius: 6px;
  margin-top: auto;
  padding: 10px;
}

.card button:disabled {
  cursor: not-allowed;
  background: #aaa;
}

.clear {
  background: var(--accent);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  padding: 8px 12px;
}

.compare-table {
  gap: 6px;
  margin-top: 15px;
  display: grid;
}

.row {
  background: #f9fafb;
  border-radius: 6px;
  grid-template-columns: 120px repeat(3, 1fr);
  justify-content: space-between;
  padding: 10px;
  display: grid;
}

.header {
  background: #e5e7eb;
  font-weight: bold;
}

.label {
  font-weight: bold;
}

.highlight {
  background: #dcfce7;
}

@media (width <= 768px) {
  .row {
    text-align: center;
    grid-template-columns: 1fr;
  }
}

.compare-section {
  background: var(--compare-bg);
  border-radius: 10px;
  margin-bottom: 10px;
  padding: 24px;
  position: relative;
  overflow-x: auto;
}

.compare-section .clear {
  color: #fff;
  cursor: pointer;
  background: #222;
  border: none;
  border-radius: 6px;
  margin-bottom: 12px;
  padding: 6px 12px;
  position: sticky;
  left: 0;
}

.compare-table {
  grid-auto-rows: minmax(48px, auto);
  row-gap: 6px;
  min-width: 700px;
  display: grid;
}

.compare-table .row {
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: 8px;
  grid-template-columns: 180px repeat(auto-fit, minmax(160px, 1fr));
  align-items: center;
  padding: 10px;
  display: grid;
}

.compare-table .row.header {
  background: var(--highlight);
  z-index: 2;
  font-weight: 600;
  position: sticky;
  top: 0;
}

.compare-table .label {
  color: var(--text);
  font-weight: 500;
}

.compare-table .row > div {
  color: var(--muted-text);
  padding: 6px 10px;
  font-size: .95rem;
}

.compare-table .row.highlight {
  border: 1px solid var(--accent);
  background: var(--highlight);
}

.compare-table .row.header button {
  color: #d32f2f;
  cursor: pointer;
  background: none;
  border: none;
  margin-left: 8px;
  font-weight: 200;
}

@media (width <= 1024px) {
  .compare-table .row {
    grid-template-columns: 150px repeat(auto-fit, minmax(140px, 1fr));
  }
}

@media (width <= 768px) {
  .compare-section {
    padding: 16px;
  }

  .compare-table {
    row-gap: 12px;
  }

  .compare-table .row {
    grid-template-columns: 1fr;
    gap: 8px;
  }

  .compare-table .label {
    background: var(--label-bg);
    border-radius: 6px;
    padding: 8px;
  }

  .compare-table .row > div:not(.label) {
    background: var(--valueCell-bg);
    border-radius: 6px;
    justify-content: space-between;
    padding: 8px 12px;
    display: flex;
  }

  .compare-table .row.header {
    grid-template-columns: 1fr;
    gap: 10px;
  }
}

.theme-toggle {
  background: var(--surface);
  color: var(--text);
  border: 1px solid var(--border);
  cursor: pointer;
  z-index: 100;
  border-radius: 999px;
  padding: 8px 14px;
  font-size: .9rem;
  position: fixed;
  top: 16px;
  right: 16px;
}
